html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.persons_container{margin-top:30px;display:flex;flex-flow:wrap;justify-content:center;align-items:center}.persons_container ._person{width:100px;height:150px;margin:10px;position:relative;display:flex;justify-content:center;align-items:center}.persons_container ._person.__selected{transition:transform 100ms linear;transform:scale(1.2)}.person-frame,.person-frame.__selected::before,.person-frame.__selected::after,.person-frame2,.person-frame2:hover::before,.person-frame2:hover::after{position:absolute;top:0;bottom:0;left:0;right:0}.person-frame{width:100px;height:150px;margin:auto;color:#b80d57;box-shadow:inset 0 0 0 2px rgba(184,13,87,0.5)}.person-frame.__selected::before,.person-frame.__selected::after{content:'';z-index:-1;margin:-5px;box-shadow:inset 0 0 0 2px;-webkit-animation:clipMe 1.5s linear infinite;animation:clipMe 1.5s linear infinite}.person-frame.__selected::before{-webkit-animation-delay:-.75s;animation-delay:-.75s}.person-frame2{width:100px;height:150px;margin:auto;color:#b80d57;box-shadow:inset 0 0 0 2px rgba(184,13,87,0.5)}.person-frame2:hover::before,.person-frame2:hover::after{content:'';z-index:-1;margin:-5px;box-shadow:inset 0 0 0 2px;-webkit-animation:clipMe 1.5s linear infinite;animation:clipMe 1.5s linear infinite}.person-frame2:hover::before{-webkit-animation-delay:-.75s;animation-delay:-.75s}@-webkit-keyframes clipMe{0%,100%{clip:rect(0, 110px, 5px, 0);color:#eda0f0}25%{clip:rect(0, 5px, 160px, 0);color:#f5fcc1}50%{clip:rect(155px, 110px, 160px, 0);color:#bae5e5}75%{clip:rect(0, 110px, 160px, 105px);color:#6fc9e7}}@keyframes clipMe{0%,100%{clip:rect(0, 110px, 5px, 0);color:#eda0f0}25%{clip:rect(0, 5px, 160px, 0);color:#f5fcc1}50%{clip:rect(155px, 110px, 160px, 0);color:#bae5e5}75%{clip:rect(0, 110px, 160px, 105px);color:#6fc9e7}}.big-person-container{position:absolute;top:0;left:0;z-index:-1}.big-person-container .__item{transform:scale(0.3)}.big-person-container .__item.show{transform:scale(1);transition-duration:0.3s}.big-person-container .__img{width:500px;height:700px;background-position:center;background-size:cover;background-repeat:no-repeat}.test-container{display:flex;justify-content:center;align-items:center;flex-flow:wrap}.test-container .item{width:100px;height:100px;background-color:blue;display:flex;justify-content:center;align-items:center;margin:50px}.test-container .item .child-item{width:100px;height:100px;text-align:center;background-color:red}.test-container .item .child-item:hover{transition:transform 100ms linear;transform:scale(1.2)}.test-container .item .child-item+.e1{background-color:black}.person-name-wapper{position:absolute;height:100%;width:100%;top:0;left:0;display:flex;justify-content:center;align-items:center}.text-1{font-size:3rem;font-family:'Russo One', sans-serif;color:rgba(88,88,88,0.158);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:-2px -2px 10px #202020;-webkit-animation-name:mini;animation-name:mini;-webkit-animation-duration:5s;animation-duration:5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;transform:translateX(-100px) scale(1.5);opacity:0}.text-1.__selected{transition-duration:400ms;transform:translateX(0) scale(1);opacity:1}@-webkit-keyframes mini{0%,100%{color:black;text-shadow:-2px -2px 10px #202020}20%{color:#3900a3}40%{}50%{text-shadow:10px -2px 20px #ffa1ff}60%{color:#005469}80%{color:#3b3b3b;color:#7a001f;text-shadow:20px 0px 30px #ffee50}}@keyframes mini{0%,100%{color:black;text-shadow:-2px -2px 10px #202020}20%{color:#3900a3}40%{}50%{text-shadow:10px -2px 20px #ffa1ff}60%{color:#005469}80%{color:#3b3b3b;color:#7a001f;text-shadow:20px 0px 30px #ffee50}}html{font-size:16px}._video{height:100vh;width:100vw;float:left;top:0;padding:none;object-fit:cover;position:fixed}._test_container{position:absolute;top:0;left:0;z-index:10}*,*::before,*::after{box-sizing:border-box}

